import { useCallback } from 'react'
import { useDispatch, useSelector } from 'react-redux'
import { RootState } from '../../root'
import { closeDialog } from '../../store/actions/dialogs'
import { MajarraButton } from '../button'
import { Cell } from '../grid/Cell'
import { BaseModal } from './BaseModal'


export function InfoModal() {

    const isOpen = useSelector((state: RootState) => state.dialogs.info);
    const dispatch = useDispatch();
    const handleClose = useCallback(() => dispatch(closeDialog({ dialogName: 'info' })), []);

    return (
        <BaseModal title=" " isOpen={isOpen} handleClose={handleClose}>
            <p className="text-sm text-gray-500 dark:text-gray-300 mb-4">
                {'              '}
            </p>


            <p className="text-sm text-gray-500 dark:text-gray-300">
                {' ()       '}
            </p>
            <div className="flex justify-center mb-4 mt-1">
                <Cell value="" status="correct" />
                <Cell value="" />
                <Cell value="" />
                <Cell value="" />
                <Cell value="" />
            </div>

            <p className="text-sm text-gray-500 dark:text-gray-300">
                {' ()         '}
            </p>
            <div className="flex justify-center mb-4 mt-1">
                <Cell value="" />
                <Cell value="" />
                <Cell value="" status="present" />
                <Cell value="" />
                <Cell value="" />
            </div>

            <p className="text-sm text-gray-500 dark:text-gray-300">
                {' ()    '}
            </p>
            <div className="flex justify-center mb-4 mt-1">
                <Cell value="" />
                <Cell value="" />
                <Cell value="" />
                <Cell value="" status="absent" />
                <Cell value="" />
            </div>

            <div className="w-[250px] mx-auto my-5">
                <MajarraButton
                    onClick={handleClose}
                    variant="contained"
                    className="h-[40px]"
                    fullWidth>
                    {'  '}
                </MajarraButton>
            </div>

            <div
                className="flex justify-center align-center pt-4 py-2"
            >
                <p className="text-black">
                    {`Copyright  ${new Date().getFullYear()} Majarra`}
                </p>
            </div>
        </BaseModal>
    )
}
